.header-color {
  background: #0575e6; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to bottom,
    #021b79,
    #0575e6
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #021b79,
    #0575e6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.page-content {
  color: white;
}

.particles {
  position: fixed !important;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.banner-text {
  /* background-color: #0575e6;
  opacity: 0.3; */
  /* width: 75%; */
  margin: auto;
  border-radius: 10px;
  text-align: center;
  font-family: "Montserrat", sans-serif;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: medium;
  color: white;
  font-family: "Montserrat", sans-serif;
}

.banner-text h2 {
  font-size: 48px;
  font-weight: medium;
  margin-bottom: 0.5rem;
  color: white;
  font-family: "Montserrat", sans-serif;
}

.banner-text hr {
  border-top: 3px dotted white;
  width: 30%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
}

.banner-text i {
  font-size: 2em;
  color: white;
  padding: 0.5em;
}

.projects-grid {
  display: flex;
  flex-flow: row wrap;
}

/* about page css */
.about-text {
  font-size: 2em;
  color: white;
  margin: 1.5em;
  padding-right: 20%;
  padding-left: 20%;
}

/* contact page css */

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 80%;
  background: white;
  opacity: 0.5;
  color: black;
  border: none;
  margin-bottom: 20px;
  border-radius: 10px;
}

.contact-grid h2 {
  font-family: "Raleway", sans-serif;
  font-weight: medium;
}

.contact-grid hr {
  border-top: 3px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 25px;
  font-style: bold;
  padding-left: 20rem;
}
.contact-list i a {
  text-decoration: none;
  color: black;
}
.resumeA img {
  margin: auto;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 950px) {
  .contact-list i {
    font-size: 20px;
    padding-left: 0rem;
  }
}

/* contact page css */

.social-links {
  color: white;
}

/* buttons hyperlink on project page */
.linkButton a {
  color: "#0000cc";
  text-decoration: none;
}

